html, body {
    min-height: 100%;
}
body { background-color: dodgerblue; }

.navbar-brand{
    font-size: 45px;
    font-weight: 600;
    margin-left: 110px;
}
.navbar-nav{
    margin-left: 10rem;
    padding-top: 2rem;
}
.navbar-nav li{
    margin-right: 10px;
    font-size: 20px;
}
.navbar-nav li:hover{
    background-color: #035683;
    border-radius: 5px;
    color: white;
}

#active-link{
    background-color: #035683;
    border-radius: 5px;
}
.navbar-nav a#active-link{
    color: white;
}

.navbar-email-list{
  display: flex;
  justify-content: end;
}
.navbar-email-list li{
  color: white;
  padding-right: 2rem;
  font-size: 20px;
  list-style: none;
}
#homeimage{
    width: 100%;
    height: 400px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.image{
    position: relative;
    text-align: center;
    color: rgb(34, 31, 31);
  
}
.imgtxt{
  z-index: 1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.homeinfo{
    display: grid;
    grid-template-columns: 4fr 4fr 4fr;
    justify-content: center;
    padding: 20px 150px 20px 150px;
}
.minhome{
    padding-left: 15px;
}
.minhome h4{
    color: white;
    font-weight: 500;
}
.minhome p{
    color: white;
}

.homefooter{
    background-color: black;
    height: 30px;
   
}
.homefooter p{
    text-align: center;
    font-size: 16px;
    padding-top: 7px;
    color: #7d7c7c;
    line-height: 10px;
}
.about-img{
   width: 50%;
   height: 150px;
   /* float: right; */
   position: absolute;
   top: 0;
   right: 0;
}
.about-head h1{
    color: white;
    padding: 30px 100px 20px 110px;
}
.about-head{
    position: relative;
    height: 150px;
    width: 100%;
    background-color: darkorchid;
}
.mcontent-1{
    background-color: white;
    display: grid;
    grid-template-columns: 4fr 8fr;
    padding: 20px 100px 20px 100px;

}
.about-img1{
    box-shadow: 0 10px 6px -6px #777;
    margin: 5px;
    padding-left: 10px;
}
.about-info{
padding: 12px;
}
.about-info p{
    text-align: justify;
}
.about-info h3{
    margin-top: 10px;
    font-size: 22px;
}
.mcontent-2{
    background-color: white;
    display: grid;
    grid-template-columns: 6fr 6fr;
    padding: 20px 100px 20px 100px;


}
.mcontent-3{
    background-color: white;
    display: grid;
    grid-template-columns: 6fr 6fr;
    padding: 20px 100px 20px 100px;

}
.mcontent-3 h3{
    color: #035683;
}
.mcontent-3 p{
    text-align: justify;
}
.middle{
    display: flex;  
    margin-right: 10px;
}
.middle h3{
    color: #035683;
}
.middle p{
    text-align: justify;
}
.middle img{
    margin: 10px;
    box-shadow: 0 10px 6px -6px #777;
}
.middle1{
    display: flex;
    margin-left: 20px;
}
.middle1 p{
    text-align: justify;
}
.middle1 h3{
    color: #035683;
}
.middle1 img{
    margin: 10px;
    box-shadow: 0 10px 6px -6px #777;
}
.service-info{
    background-color: white;
    display: grid;
    grid-template-columns: 3fr 8fr;
    padding: 20px 100px 20px 100px;
    height: 450px;
}
.service-info img{
    box-shadow: 0 10px 6px -6px #777;
    width: 400px;
    height: 300px;
    padding-right: 10px;
}
